<template>
  <div class="tags">
    <router-link
      :class="[ 'tag', 'tag--hovered', { 'tag--active': isActive(i) } ]"
      :to="noRoute ? {} : { name: 'home' }"
      tag="span"
      v-for="(item, i) in getItems"
      :key="i"
      @click.native="toggle(i)"
    >
      {{ item }}
    </router-link>

    <span class="tag" v-if="more > 0">+{{ more }}</span>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'

export default {
  name: 'c-tags',

  props: {
    items: {
      type: Array,
      required: true
    },
    limit: {
      type: [String, Number],
      default: null,
      validator: v => v && Number(v) > 0
    },
    noRoute: {
      type: Boolean,
      default: false
    }
  },

  data: () => ({
    more: 0
  }),

  created () {
    if (this.limit) {
      this.more = this.items.length - this.limit
    }
  },

  computed: {
    ...mapGetters({
      getActive: 'search/getTags'
    }),
    getItems () {
      if (this.limit) {
        return this.items.slice(0, this.limit)
      }

      return this.items
    }
  },

  methods: {
    toggle (i) {
      if (this.isActive(i)) {
        this.$store.commit('search/delTag', this.items[i])
      } else {
        this.$store.commit('search/addTag', this.items[i])
      }
    },

    isActive (i) {
      return this.getActive.find(item => item === this.items[i])
    }
  }
}
</script>

<style scoped>
  .tags {
    display: flex;
    flex-wrap: wrap;
    margin: -.2rem 0 0 -.5rem;
  }

  .tag {
    text-decoration: none;
    padding: .2rem .4rem;
    margin: .1rem;
    font-size: inherit;
    font-weight: 400;
    color: hsl(var(--ui-primary-h), var(--ui-primary-s), 70%);
    border-radius: 1rem;
    user-select: none;
    transition: color .2s, background .2s;
  }
  .tag--hovered:hover {
    color: hsl(var(--ui-active));
    cursor: pointer;
  }
  .tag--active {
    color: hsl(var(--ui-active));
    background: hsl(var(--ui-active-h), var(--ui-active-s), 97%);
  }
</style>